@import url("reference.css");

.sandbox li {
    list-style: none;
}

.param-list .param-list-group {
    margin-top: 8px;
    border-top: 1px solid #e1e8ed;

}

.param-list .param-list-group > ol, ul {
    list-style: none;
}

.param-list .param-list-group .param-list-item {
    padding: 17px 0;
    border-bottom: 10px solid #f0f4f7;
    line-height: 24px;
}

.param-list-item-label {
    position: relative;
    z-index: 5;
    float: left;
    text-align: left;
    width: 80px;
    margin-bottom: 0px;
    margin-top: 0px;
}

.param-list-item-label {
    text-transform: none;
    font-size: 13px;
    line-height: 21px;
    word-break: break-all;
    white-space: normal;

}

.param-list-item-input {
    position: relative;
    z-index: 10;
    background: #fff;
    margin: 0 0 0 100px;
    font-size: 13px;
    line-height: 21px;
}

.param-list-item-input input, textarea {
    width: 100%
}

.param-list-item-input textarea {
    height: 10em;
}

.sandbox pre {
    margin-top: 5em;
    margin-left: 21px;
    padding: 0em;
    width: 100%;
}

.sandbox ul li,ol li {
    margin: 0em;
}
.sandbox .param-list-item:last-child {
    margin-top: 1em;
    margin-bottom: 1em;
}

.sandbox pre {
    border-radius: 2px;
    border-width: 1px;
    border-style: solid;
    overflow: auto;
    padding: 1em;
    margin: 0em;
    font-size: 0.9em;
    line-height: 1.2;
    direction: ltr;
    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    word-wrap: normal;
    -moz-tab-size: 4;
    -o-tab-size: 4;
    tab-size: 4;
    -webkit-hyphens: none;
    -moz-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: -ms-autohiding-scrollbar;
    border-color: rgba(0,0,0,0.05);
    color: #ddd;
    background-color: #252821
}

.wordbreak {
    word-break: break-all;display: inline;word-wrap: break-word;
}

.sandbox a.button:hover {
    transform: translateY(0px);
}

.animation-progress-ring-20x20 {
    width: 20px;
    height: 20px;
    background-image: url("../images/progress-ring-20x20.png");
    animation: keyframes-progress-ring-20x20 3.2s steps(75) infinite;
}

.animation-progress-ring-white-20x20 {
    width: 20px;
    height: 20px;
    background-image: url("../images/progress-ring-white-20x20.png");
    animation: keyframes-progress-ring-20x20 3.2s steps(75) infinite;
}

@keyframes keyframes-progress-ring-20x20 {
    from {
        background-position: 0px;
    }
    to {
        background-position: -1500px;
    }
}

@-o-keyframes keyframes-progress-ring-20x20 {
    from {
        background-position: 0px;
    }
    to {
        background-position: -1500px;
    }
}

@-moz-keyframes keyframes-progress-ring-20x20 {
    from {
        background-position: 0px;
    }
    to {
        background-position: -1500px;
    }
}

@-webkit-keyframes keyframes-progress-ring-20x20 {
    from {
        background-position: 0px;
    }
    to {
        background-position: -1500px;
    }
}